import Vue from 'vue'
import VueRouter from 'vue-router'
import BaseLayout from '@/views/layout/BaseLayout'
import RouterView from '@/views/layout/RouterView'

Vue.use(VueRouter)

// hack router push callback
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location, onResolve, onReject) {
  if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
  return originalPush.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: ()=>import('@/views/login/Login')
  },
  {
    path: '/',
    redirect: '/dashboard'
  },
  {
    path: '/home',
    name: 'Home',
    redirect: '/dashboard',
    component: BaseLayout,
    children: [
      {
        path: '/dashboard',
        name: 'Dashboard',
        meta: {title: '首页'},
        component: () => import('@/views/dashboard/Dashboard')
      },
      {
        path: '/entercustomer',
        name: 'EnterCustomer',
        meta: {title: '录入客户'},
        component: () => import('@/views/entercustomer/EnterCustomer')
      },
      {
        path: '/customers',
        name: 'Customers',
        meta: {title: '客户列表'},
        component: RouterView,
        redirect: '/totalcustomer',
        children:[
          {
            path: '/totalcustomer',
            name: 'TotalCustomer',
            meta: {title: '全部客户'},
            component: () => import('@/views/customer/TotalCustomer')
          },
          {
            path: '/personalcustomer',
            name: 'PersonalCustomer',
            meta: {title: '个人客户'},
            component: () => import('@/views/customer/PersonalCustomer')
          },
          {
            path: '/publiccustomer',
            name: 'PublicCustomer',
            meta: {title: '公共客户'},
            component: () => import('@/views/customer/PublicCustomer')
          }
        ]
      },
      {
        path: '/followup',
        name: 'FollowUp',
        meta: {title: '跟进列表'},
        component: () => import('@/views/followup/FollowUp')
      },
      {
        path: '/departments',
        name: 'Departments',
        meta: {title: '部门管理'},
        component: () => import('@/views/departments/Departments')
      },
      {
        path: '/staffs',
        name: 'Staffs',
        meta: {title: '员工管理'},
        component: () => import('@/views/staffs/Staffs')
      }
    ]
  },
  {
    path: '*',
    name: 'Error',
    component: () => import('@/views/error/Error')
  }
]

const router = new VueRouter({
  mode: 'hash',
  routes
})

export default router
