<template>
  <div class="tab-content w100">
    <el-tabs v-model="activeTab" type="border-card"
             @tab-click="tabClick" @tab-remove="tabRemove"
    >
      <el-tab-pane
        v-for="item in tabs"
        :key="key(item)"
        :label="item.title"
        :name="item.route"
        :closable="true"
      >
        <router-view />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
  export default {
    data() {
      return {
      }
    },
    computed: {
      activeTab: {
        get() {
          return this.$store.state.global.activeTab
        },
        set(val) {
          this.$store.commit("global/set_active_tab", val)
        }
      },
      tabs() {
        return this.$store.state.global.tabs
      },
      key() {
        return (item) => JSON.stringify(item)
      }
    },
    methods: {
      tabClick(tab, event) {
        let path = this.activeTab;
        this.$router.push({path})
      },
      tabRemove(targetName) {
        let _this = this;
        if(_this.tabs.length == 1) {
          if(targetName == '/dashboard') {
            return false
          } else {
            _this.$store.commit("global/delete_tabs", targetName);
            _this.$router.push({path: "/dashboard"});
          }
        } else {
          _this.$store.commit("global/delete_tabs", targetName);
          _this.$router.push({path: _this.tabs[this.tabs.length - 1].route})
        }
      }
    },

    watch: {
      $route(to, from) {
        let flag = false;
        for (let tab of this.tabs) {
          if(tab.route == to.path) {
            flag = true
            this.$store.commit("global/set_active_tab", to.path);
            break;
          }
        }
        if(!flag) {
          let data = {
            route: to.path,
            name: to.name,
            title: to.meta.title
          }
          this.$store.commit("global/add_tabs", data)
          this.$store.commit("global/set_active_tab", to.path)
        }
      }
    }
  }
</script>
<style lang="css" scoped>
  .tab-content >>> .el-tabs{
    height: 100%;
  }
  .tab-content >>> .el-tabs__header {
    height: 40px;
  }
  .tab-content >>> .el-tabs__content {
    height: calc(100% - 40px);
    overflow: auto;
    box-sizing: border-box;
  }
  .tab-content >>> .el-tabs__content >>> .el-tab-pane{
    height: 100%;
    overflow: auto;
  }
</style>
<style lang="less">
  .tab-content{
    height: 100%;
  }
</style>
