<template>
  <div class="header-content w100 h100">
    <div class="fold">
      <i v-if="isCollapse" class="el-icon-s-fold pointer" @click="handleFold(false)"></i>
      <i v-else class="el-icon-s-unfold pointer" @click="handleFold(true)"></i>
    </div>
    <el-breadcrumb separator="/" class="breadcrumb-content">
      <el-breadcrumb-item v-for="(v,i) in breadcrumbs" :key="i">
        <span v-if="breadcrumbs.length==1">{{v.meta.title}}</span>
        <span v-else-if="i==0">{{v.meta.title}}</span>
        <span v-else>{{v.meta.title}}</span>
      </el-breadcrumb-item>
    </el-breadcrumb>
    <div class="logout">
      <el-dropdown>
        <span class="el-dropdown-link">
         <i class="el-icon-plus cur mr15" style="font-size: 20px;" />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>
            <span style="display:block;" @click="opera(1)">录客户</span>
          </el-dropdown-item>
          <el-dropdown-item>
            <span style="display:block;" @click="opera(2)">写跟进</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-dropdown>
        <span class="el-dropdown-link">
          <i class="el-icon-setting cur" style="font-size: 20px;"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>
            <span style="display:block;" @click="opera(3)">个人资料</span>
          </el-dropdown-item>
          <el-dropdown-item>
            <span style="display:block;" @click="opera(4)">修改密码</span>
          </el-dropdown-item>
          <el-dropdown-item>
            <span style="display:block;" @click="logout">退出登陆</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>

    <el-dialog title="新增跟进记录" :visible.sync="dialogFormVisible" class="add-follow">
      <el-form :model="followForm" ref="followForm">
        <el-form-item label="客户名称">
          <el-autocomplete
            v-model="followForm.customerId"
            :fetch-suggestions="(queryString, cb) => queryInputAsync(queryString, cb, 'customer')"
            :placeholder="'input' | solvePlaceholder"
            @select="(val) => queryInputAsync(_, val,'customer', true)"
          />
        </el-form-item>
        <el-form-item label="跟进人">
          <el-autocomplete
            v-model="followForm.staffId"
            :fetch-suggestions="(queryString, cb) => queryInputAsync(queryString, cb, 'followMan')"
            :placeholder="'input' | solvePlaceholder"
            @select="(val) => queryInputAsync(_,val,'followMan', true)"
          />
        </el-form-item>
        <el-form-item label="跟进结果">
          <el-input v-model="followForm.result" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
      </el-form>

      <span slot="footer">
        <el-button type="primary">保存</el-button>
        <el-button @click="e=>dialogFormVisible=false">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        breadcrumbs: [],
        dialogFormVisible: false,
        followForm: {
          customerId:'',
          staffId:'',
          result: ''
        }
      }
    },
    created() {
      this.getBreadCrumb()
    },
    props: {
      isCollapse:{
        type: Boolean,
        default: () => false
      }
    },
    methods: {
      handleFold(type) {
        this.$emit("callback", type)
      },
      getBreadCrumb() {
        let list = this.$route.matched.filter(v => v.meta&&v.meta.title)
        this.breadcrumbs = list
      },
      opera(type) {
        let _this = this
        switch (type) {
          case 1:
            _this.$router.push({path: '/entercustomer'})
            break;
          case 2:
            _this.dialogFormVisible = true
            break;
          case 3:
            break;
          case 4:
            break;
          default:
            break
        }

      },
      logout() {
        this.$router.push({path: '/login'})
      },
      queryInputAsync(queryString, val, type, isSelect) {
        console.log('queryString', queryString)
        console.log('val', val)
      }
    },
    watch: {
      $route(to, from){
        this.getBreadCrumb()
      }
    }
  }
</script>
<style lang="less">
  .header-content{
    display: flex;
    justify-content: flex-start;
    .fold{
      padding-top: 16px;
      margin-right: 40px;
      .pointer{
        font-size: 28px;
        cursor: pointer;
      }
    }
    .breadcrumb-content{
      line-height: 60px;
    }
    .logout{
      line-height: 60px;
      text-align: right;
      flex: 1;
    }
  }
</style>

