<template>
  <div class="aside-content">
    <div class="logo-box">
      <div class="avatar">
        <img :src="avatarImg" alt="用户头像">
      </div>
      <div class="avatar-tip" v-show="!isCollapse">张三</div>
    </div>
    <el-menu router ref="menu"
             :default-active="$route.path"
             background-color="#545c64"
             text-color="#fff"
             active-text-color="#ffd04b"
             :collapse="isCollapse"
             class="el-menu-vertical-demo"
    >
      <el-menu-item index="/dashboard"><i class="el-icon-setting"></i><span slot="title">首页</span></el-menu-item>
      <el-menu-item index="/entercustomer"><i class="el-icon-setting"></i><span slot="title">录入客户</span></el-menu-item>

      <el-submenu index="/powerManager" >
        <template slot="title"><i class="el-icon-setting"></i><span slot="title">客户列表</span></template>
        <el-menu-item index="/totalcustomer">全部客户</el-menu-item>
        <el-menu-item index="/personalcustomer">个人客户</el-menu-item>
        <el-menu-item index="/publiccustomer">公共客户</el-menu-item>
      </el-submenu>
      <el-menu-item index="/followup"><i class="el-icon-setting"></i><span slot="title">跟进列表</span></el-menu-item>
      <el-menu-item index="/departments"><i class="el-icon-setting"></i><span slot="title">部门管理</span></el-menu-item>
      <el-menu-item index="/staffs"><i class="el-icon-setting"></i><span slot="title">员工管理</span></el-menu-item>
    </el-menu>
  </div>
</template>
<script>
  import {mapState, mapMutations} from 'vuex';

  export default {
    data() {
      return {
        avatarImg: ''
      }
    },
    props: {
      isCollapse:{
        type: Boolean,
        default: false
      }
    },
    computed: {
      ...mapState('user', {
        userInfo: 'userInfo'
      })
    },
    created(){
      let _this = this
      let _avatarImg = _this.userInfo.avatar || require('@/assets/img/avatar.jpg')
      _this.avatarImg = _avatarImg
    },
    mounted() {
      // 刷新时以当前路由作为tab加入tabs
      this.refshTab()
    },
    methods: {
      ...mapMutations('global', {
        add_tabs: 'add_tabs',
        set_active_tab: 'set_active_tab'
      }),
      refshTab() {
        let _this = this;
        let {path, name, meta} = _this.$route;
        if(_this.$route.meta&&_this.$route.meta.title) {
          _this.add_tabs({ route: path, name , title: meta.title })
          _this.set_active_tab(path)
        }
      }
    }
  }
</script>
<style lang="less">
  .aside-content{
    padding: 10px;
    background:#545c64;
    .logo-box {
      padding: 5px;
      background: #fff;
      border: 1px solid #aaa;
      .avatar{
        width: 60px;
        height: 60px;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .avatar-tip{
        margin-left: 15px;
      }
    }
  }

</style>
