// import LayoutContent from "@/layout/layoutContent.vue"

let whiteEmptyVal = ["",undefined,null]

// json处理接口请求参数
export const filterNullProp = vals => {
  let _vals = {};
  if (isObject(vals)) {
    for (const valsKey in vals) {
      if (!whiteEmptyVal.includes(vals[valsKey])) {
        _vals[valsKey] = vals[valsKey]
      }
    }
    return _vals
  }
  return vals
}

// 拼接参数处理
export const formData = vals => {
  let result = ''
  Object.keys(params).forEach((key) => {
    if (!Object.is(vals[key], undefined) && !Object.is(vals[key], null)) {
      result += encodeURIComponent(key) + '=' + encodeURIComponent(vals[key]) + '&'
    }
  })
  return result
}

// 判断是否为一个对象
export const isObject = value => (Object.prototype.toString.call(value) === "[object Object]")

// 判断是否为一个非空非undefined非null的值
export const isVaild = value => !whiteEmptyVal.includes(value)

// 返回错误提示
export const errorTip = error => error || '该项为必填项'

// sessionStorage
export const operaStorage = (type, key, value) => {
  let _storage
  switch (type) {
    case 'set':
      sessionStorage.setItem(key, value)
      break;
    case 'get':
      _storage = sessionStorage.getItem(key)
      break;
    case 'remove':
      sessionStorage.removeItem(key)
      break;
    case 'clear':
      sessionStorage.clear()
      break;
    default:
      _storage = false
      break;
  }
  return _storage
}

