import {isObject, operaStorage} from "@/utils/tools";
import {Message} from "element-ui";

export default {
  namespaced: true,
  state: ()=>({
    whiteEmptyVal: ["",undefined,null],
    roles: [1,2,3,4],
    activeTab: "",
    tabs: [],
    setting:{
      menus: JSON.parse(operaStorage('get','menus')||'[]')
    }
  }),
  actions:{

  },
  mutations: {
    add_tabs(state, data) {
      let tabs = state.tabs,
        flag = false,
        tData=[];
      let isSameTab = tabs.findIndex(o => o.route == data.route);
      isSameTab==-1 && (data.route == "/dashboard" ? state.tabs.unshift(data) : state.tabs.push(data))
    },
    set_active_tab(state, path) {
      state.activeTab = path
    },
    delete_tabs(state, route) {
      let index = 0;
      for (let tab of state.tabs) {
        if(tab.route === route) {
          break;
        }
        index++
      }
      state.tabs.splice(index, 1)
    },
    config_setting(state, sets) {
      if (isObject(sets)) {
        for (const setsKey in sets) {
          state.setting[setsKey] = sets[setsKey]
        }
      }
    },

  },
}
